@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.fancybox-navigation .fancybox-button--arrow_left {
    padding: 20px 15px 20px 6px;

}
.fancybox-navigation .fancybox-button--arrow_right {
    padding: 20px 6px 20px 15px;
}
.offer-modal__wrapper {
    & * {
        font-family: 'Myriad Pro' !important;
        font-weight: 400;
        font-style: normal;
    }
    b {
        font-weight: 700;
    }
    &.fancybox-content {
        padding: 0 !important;
    }
    .offer-modal {
        max-width: 817px;
        width: 100%;
    }
    .offer-modal__header {
        position: relative;
        // padding: 18px 20px 17px;
        padding: 31px 20px 17px;
        box-sizing: border-box;

        font-style: normal;
        font-weight: 400;
        // font-size: 40px;
        font-size: 32px;
        line-height: 135%;
        /* identical to box height, or 54px */
        text-align: center;
        
        color: #37383D;            
    }
    .close-modal {
        width: 47px;
        height: 47px;
        border: none;
        outline: none;
        background: $main-color;
        display: flex;
        justify-content: center;
        align-items: center;

        position: absolute;
        right: 0;
        top: 0;
    }
    .offer-modal__body {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .offer-modal__body__left {
        max-width: 408px;
        width: 100%;
        min-height: 268px;
        position: relative;
        box-sizing: border-box;
        padding: 30px 10px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        background: $main-color;

        &::after {
            content: '';
            display: block;
            // height: 105%;
            // width: 100px;

            border: 68px solid transparent;
            border-left: 24px solid $main-color;
            border-top: 200px solid $main-color;

            position: absolute;
            top: 0;
            left: 100%;
            // transform: rotate(14deg);
        }
    }
    .offer-modal__body__header {
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 140%;
        /* or 34px */
        text-align: center;
        letter-spacing: 0.3em;
        text-transform: uppercase;
        
        // margin-bottom: 26px;
        color: #fff;

    }
    .offer-modal__body__value {
        max-width: 408px;
        font-size: 33px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
        color: #fff;
        margin-bottom: 5px;
    }
    .offer-modal__body__desc {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        /* or 22px */
        text-align: center;
        color: #FFFFFF;        
    }

    .offer-modal__body__right {
        background-image: url('../img/modal-offer-bg.jpg');
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .offer-modal__body__text {
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 100%;
        /* or 40px */
        color: #37383D;
        max-width: 249px;
        width: 100%;
        position: relative;

        &::after {
            content: '';
            display: block;
            width: 73px;
            height: 5px;
            background: $main-color;

            position: absolute;
            top: 120%;
        }
    }
    .offer-modal__bottom {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-direction: column;
        padding: 18px 15px 25px; 
    }
    .offer-modal__bottom__date {
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 120%;
        /* identical to box height, or 24px */
        text-align: center;
        color: #37383D;   
        margin-bottom: 6px;  
    }
    .offer-modal__bottom__phone {
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        /* identical to box height, or 29px */
        text-align: center;
        color: #37383D;
        text-decoration: none;
        margin-bottom: 16px;
    }
    .offer-modal__bottom__wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    .offer-modal__bottom__orange {
        @include page-btn();
        max-width: 217px;
        width: 100%;
        border: 1px solid $main-color;

        font-style: normal;
        font-weight: 600;
        font-size: 13px;
        line-height: 16px;
        text-transform: uppercase;
        color: #FFFFFF;    
        text-decoration: none;    
        margin-right: 15px;
    }
    .offer-modal__bottom__transparent {
        @include page-btn-transparent();
        border: 1px solid $main-color;
        max-width: 217px;
        width: 100%;

        font-style: normal;
        font-weight: 600;
        font-size: 13px;
        line-height: 16px;
        text-transform: uppercase;
        text-decoration: none;    
        
        color: #B34215;   
        &:hover {
            border: 1px solid $main-color;

        }     
    }
}

.catalog-item-page {
    background-color: #fff;

    * {
        font-family: 'Myriad Pro' !important;
        font-weight: 400;
        font-style: normal;
        color: $text-main-color;
        text-decoration: none;

        min-height: 0;
        min-width: 0;
    }

    .btn {
        @include page-btn();
    }

    .info-trigger {
        cursor: pointer;
        width: 10px;
        height: 10px;
        background: #D9D9D9;
        border-radius: 50%;

        display: flex;
        justify-content: center;
        align-items: center;

        position: absolute;
        top: 7px;
        right: -11px;

        &::before {
            content: 'i';
            display: block;
            color: $main-color;
            font-size: 8px;
            position: relative;
            // right: 0.5px;
            right: 0px;
            bottom: 2px;
        }

        &:hover .info-content {
            visibility: visible;
        }
    }

    .info-content {
        background-color: #fff;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        padding: 10px 14px;
        max-width: 259px;
        width: 259px;
        position: absolute;
        top: 3px;
        left: 5px;
        z-index: 10;

        visibility: hidden;
        pointer-events: none;

        .info-list {
        }

        .info-list__item {
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;
            /* or 14px */
            color: #000000;
            text-align: left;

            &::before {
                content: '•';
                color: #000;
                font-weight: 700;
                font-size: 10px;
                line-height: 140%;
                display: inline;
            }
        }

        .info__img {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover
        }

        .info__header {
            font-style: normal;
            font-weight: 600 !important;
            font-size: 14px !important;
            line-height: 140% !important;

            color: #000000;
            display: block;
            margin-bottom: 4px;
            margin-top: 154px;
        }

        .info__desc {
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;

            color: #000000;
        }
    }

    .section-header {
        @include section-header();
    }

    .section-text {
        @include section-text();
    }
    .transparent-slider-btn {
        background: transparent;
        border: none;
        outline: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 41px;
        cursor: pointer;
        z-index: 100;

        &:last-child {
            right: 0;
            transform: scale(-1) translateY(50%);
        }
    }

    .container {
        max-width: 1140px;
        width: 100%;
        margin: 0 auto;
        padding: 0 15px;
    }

    

    .item-title {
        .container {

        }
        .item-title__header {
            text-align: left;
            margin-top: 26px;
            line-height: 140%;
        }
        .item-title__desc {
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            /* or 25px */
            color: #37383D;
            margin-bottom: 15px;
            display: block;
        }
        .item-title__wrapper {
            display: grid;
            grid-template-columns: 850px 270px;
            column-gap: 20px;
        }

        .item-title__slider-box {

        }
        .item-title__slider__wrap {
            position: relative;

            &:hover .item-title__nav-slider__wrap {
                opacity: 1;
            }
        }
        .item-title__slider__controlls {

        }
        .item-title__slider__controll {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            border: none;
            outline: none;
            cursor: pointer;

            width: 36px;
            height: 42px;
            background: #fff;
            z-index: 100;

            img {
                height: 23px;
            }

            &:last-child {
                right: 0;
                transform: scale(-1) translateY(50%);
            }
        }

        .item-title__main-slider {

        }
        .item-title__main-slider__slide {
            position: relative;
        }
        .main-slide__cost__wrap {
            max-width: 110px;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;

            &._link {
                max-width: 176px;
                padding: 5px 0 6px;
                border-radius: 0px 0px 10px 0px;
                display: block;
                background: $main-color;
            }
            .info-trigger {
                top: unset;
                bottom: 3px;
                left: unset;
                right: 5px;
            }
        }
        .main-slide__cost__wrap__header {
            font-style: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 140%;
            display: block;
            padding: 7px 0 5px;
            background: #37383D;
            
            /* identical to box height, or 17px */
            text-align: center;
            text-transform: uppercase;
            
            color: #FFFFFF;       
        }
        .main-slide__cost__wrap__price {
            display: block;
            background: $main-color;
            padding-top: 5px;
            * {
                display: block;
            }
            b {
                font-weight: 700;
                font-size: 16px;
                color: #FFFFFF;            
            }
            .square {
                position: relative;
                color: #FFFFFF;
            }
            .old-price {
                color: #FFFFFF;
                text-decoration-line: line-through;                
            }
            .calc-price {
                color: #FFFFFF;
                font-weight: 400;
                font-size: 15px;
                line-height: 140%;                
            }
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            /* or 17px */
            text-align: center;
            color: #FFFFFF;            
        }
        .main-slide__offer__wrap {
            position: absolute;
            top: 0;
            right: 0;
            padding: 7px 33px;
            border-radius: 0px 0px 0px 40px;
            max-width: 172px;
            background: #C83636;
            display: flex;
            justify-content: start;
            align-items: center;
            max-width: 172px;

            &._popular {
                background: #44C554;
                img {
                    margin-right: 0px;
                }
            }

            &:hover .main-slide__offer__text {
                visibility: visible;
            }
            img {
                margin-right: 15px;
                position: relative;
                bottom: 1px;
            }
            span {
                color: #fff;
                text-align: center;
                max-width: 109px;
            }
            .main-slide__offer__text {
                pointer-events: none;
                visibility: hidden;
                transition: .3s ease-in;
                max-width: 259px;
                min-width: 259px;
                width: 100%;
                padding: 9px 14px;
                background-color: #fff;
                box-sizing: border-box;
                display: block;
                position: absolute;
                top: 100%;
                right: 10px;

                font-style: normal;
                font-weight: 300;
                font-size: 10px;
                line-height: 140%;
                
                color: #000000;  
            }
        }
        .main-slide__img {
            width: 100%;
            object-fit: cover;
            max-height: 515px;
        }

        .item-title__nav-slider__wrap {
            position: absolute;
            bottom: 0;
            width: 100%;

            transition: .3s ease-in;
            opacity: 0;
        }
        .item-title__nav-slider {
            max-height: 74px;
            max-width: 300px;
            margin: 0 auto;
        }
        .item-title__nav-slider__slide {
            max-height: 74px;
            height: 100%;
            background: #fff;
            padding: 1px 1px 0;
            box-sizing: border-box;
            position: relative;

            &.slick-current.slick-active {
                border-bottom: 2px solid $main-color;

                .item-title__nav-slider__slide__overlay {
                    opacity: 1;
                }
            }
        }
        .item-title__nav-slider__slide__overlay {
            background: rgba(0, 0, 0, 0.4);
            width: 98%;
            height: 97%;
            position: absolute;
            top: 1;
            left: 1;
            opacity: 0;
        }
        .nav-slide__img {
            max-height: inherit;
            height: 100%;
            width: 100%;
        }

        .item-title__info__wrap {
            background: #fff;
            padding: 10px 10px;
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .title__info__wrap__btn {
            max-width: 217px;
            font-weight: 600;
            font-size: 13px;
            line-height: 16px;            
        }
        .title__info__wrap__phone__wrap {
            a {
                display: block;
            }
        }
        .title__info__wrap__phone {
            font-style: normal;
            font-weight: 700;
            font-size: 19px;
            line-height: 140%;
            
            text-align: center;
            
            color: #000000;
        }
        .title__info__wrap__phone-link {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            
            /* or 17px */
            text-align: center;
            text-decoration-line: underline;
            
            color: #B34215;
        }
        .title__info__wrap__socials {
            display: flex;
            justify-content: start;
            align-items: center;
        }
        .title__info__wrap__social-link {
            // max-width: 118px;
            max-width: 130px;
            img {
                max-width: inherit;
                width: 100%;
                object-fit: contain;
            }
            &:first-child {
                margin-right: 9px;
            }
        }

        .item-title__info-box {

        }
        .item-title__info-box__links {
            display: flex;
            justify-content: end;
            align-items: center;
            margin-bottom: 28px;
        }
        .item-title__info-box__link {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #000000;
            transition: color .3s;
            &:hover {
                color: $main-color;
                .item-title__info-box__link__img-box {
                    img {
                        &:first-child {
                            opacity: 0;
                        }
                        &:last-child {
                            opacity: 1;
                        }
                    }
                }
            }            
            
            &:not(:first-child) {
                margin-left: 25px;
            }
        }
        .item-title__info-box__link__img-box {
            position: relative;
            img {
                width: 10px;
                height: 11px;
                margin-right: 7px;
                transition: opacity .3s;
                &:first-child {
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                &:last-child {
                    opacity: 0;
                }
            }
        }
        .item-title__info-box__list {

        }
        .item-title__info-box__list__item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            &:not(:last-child) {
                padding-bottom: 17px;
                border-bottom: 1px solid rgba(217, 217, 217, 0.2);
                margin-bottom: 18px;
            }
        }
        .item-title__info-box__list__item__wrap {
            display: flex;
            justify-content: start;
            align-items: center;
            img {
                margin-right: 25px;
                min-width: 22px;
            }
        }
        .item-title__info-box__list__item__name {
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            /* or 22px */
            color: #000000;
            max-width: 112px;
        }
        .item-title__info-box__list__item__val {
            max-width: 77px;
            width: 100%;

            font-style: normal;
            font-weight: 700;
            font-size: 32px;
            line-height: 140%;

            /* identical to box height, or 45px */
            text-align: center;

            color: #37383D;

            &._text, & ._text {
                font-weight: 400;
                font-size: 12px;
                line-height: 140%;              
            }
        }
        .item-title__info-box__author {
            display: block;
            width: 100%;
            box-sizing: border-box;
            border: 1px solid #E3E3E3;
            text-align: center;
            padding: 12px 24px 11px 25px;

            font-style: normal;
            font-weight: 400;
            font-size: 13px;
            line-height: 140%;
            
            /* or 18px */
            text-align: center;
            
            color: #37383D;   
            margin-top: 22px;         
        }
    }
    .slider-tabs {
        padding-top: 22px;
        .container {

        }
        .slider-tabs__wrap {
            position: relative;
        }
        .slider-tabs__nav__controlls {
            // display: flex;
            display: none;
            position: absolute;
            top: 5px;
            right: 0;
            z-index: 10;
        }
        .slider-tabs__nav__controll {
            width: 47px;
            height: 47px;
            background-color: #37383D;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 8px 0;
            outline: none;
            border: none;
            cursor: pointer;

            &:last-child {
                background-color: $main-color;
                transform: scale(-1);
            }

            img {
                filter: invert(1);
                height: 100%;
            }
        }
        .slider-tabs__nav {
            margin-bottom: 67px;
            .slick-list {
                overflow: visible !important;
            }
            .slick-track {
                transform: translate3d(0, 0, 0) !important;
            }  
        }
        .slider-tabs__nav__slide {
            background: #EBEBEB;    
            padding: 14px 0 15px; 
            position: relative;   
            cursor: pointer;   
                             
            span {
                display: block;
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 140%;
                /* or 20px */
                text-align: center;
                text-transform: uppercase;
                color: #37383D;  
            } 
            
            &.slick-current.slick-active {
                background: #37383D;

                span {
                    color: #FFFFFF;  
                }
                &::after {
                    opacity: 1;
                }
            }
            &:nth-child(3).slick-current.slick-active {
                background: $main-color;

                span {
                    color: #FFFFFF;  
                }
                &::after {
                    opacity: 1;
                }
            }
            &::after {
                content: '';
                width: 14px;
                height: 14px;
                background: #37383D;
                display: block;
                position: absolute;
                bottom: -7px;
                left: 50%;
                transform: translateX(-50%) rotate(45deg);
                opacity: 0;
            }
            &:nth-child(3)::after {
                background: $main-color;
            }
        }
        .slider-tabs__main {
            
        }
        .slider-tabs__main__slide {

        }
        .slider-tabs__main__slide__text {
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 140%;
            /* or 21px */
            color: #37383D; 
            margin-bottom: 43px;      
            
            .hidden-text {
                font-style: normal;
                font-weight: 300;
                font-size: 15px;
                line-height: 140%;
                /* or 21px */
                color: #37383D; 
                display: none;    
                
                &._active {
                    display: inline;
                }
            }
            .readmore-trigger {
                font-style: normal;
                font-weight: 300;
                font-size: 14px;
                line-height: 160%;
                /* or 22px */
                color: $main-color;    
                text-decoration: underline;
            }
        }
        .slider-tabs__main__slide__fancy-wrap__box {
            position: relative;
        }
        .site-select__slider-wrap__controlls {
            display: none;
        }
        .slider-tabs__main__slide__fancy-wrap {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            column-gap: 11px;
            margin-bottom: 70px;
        }
        .slider-tabs__main__slide__img-link {
            text-align: center;
            transition: transform .3s;
            &:hover {
                transform: scale(1.1);
            }
            img {
                max-width: 367px;
                width: 100%;
                display: block;
                object-fit: contain;
            }
        }
        .slider-tabs__main__slide__feed-back {
            display: grid;
            grid-template-columns: 3fr 7fr 6fr;
        }
        .slider-tabs__main__slide__feed-back__img-box {

        }
        .slider-tabs__main__slide__feed-back__img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .slider-tabs__main__slide__feed-back__wrap {
            border: 1px solid $main-color;
            padding: 24px 31px;
            box-sizing: border-box;
            &._orange {
                background: $main-color;
            }
            &._orange * {
                color: #fff;
            }
        }
        .slider-tabs__main__slide__feed-back__header {
            display: block;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 120%;
            /* or 29px */
            color: #1B2833;
            margin-bottom: 10px;
        }
        .slider-tabs__main__slide__feed-back__text {
            display: block;
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 140%;
            /* or 21px */
            color: #37383D;
            margin-bottom: 20px;
        }
        .slider-tabs__main__slide__feed-back__link {
            display: block;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 140%;
            /* or 25px */
            color: $main-color;
            // transition: all .3s;

            &:hover {
                font-weight: 700;
                &::after {
                    margin-left: 13px;
                }
            }
            &::after {
                content: '→';
                display: inline;
                margin-left: 5px;
            }
        }
        .slider-tabs__main__slide__slider__wrap {
            position: relative;
        }
        .slider-tabs__main__slide__slider {
            max-height: 510px;
            height: 100%;
            padding: 0 55px;
        }
        .slider-tabs__main__slide__slider__slide {
            img {
                max-height: 510px;
                height: 100%;
